import { useState } from 'react';
import { getUsersByParams, addUser } from '~/utils';
import configPaths from '~/routes/configPaths';
function Register() {
    const [username, setUsername] = useState('');
    const [password, setPassword] = useState('');
    const [repassword, setRepassword] = useState('');
    function handleRegister(e) {
        if (username && password && repassword) {
            getUsersByParams({ username })
                .then(function (data) {
                    if (data.length > 0) {
                        console.log('Ti khon  tn ti');
                    } else if (repassword !== password) {
                        console.log('Nhp li mt khu khng chnh xc!!!');
                    } else {
                        addUser({
                            username,
                            password,
                        })
                            .then(function (response) {
                                console.log('To ti khon thnh cng, chuyn qua ng nhp!');
                                console.log(response);
                            })
                            .catch(function (error) {
                                // handle error
                                console.log(error);
                            });
                    }
                })
                .catch(function (error) {
                    // handle error
                    console.log(error);
                });
        } else {
            alert('Vui lng nhp y  thng tin');
            if (!username) document.getElementById('username').focus();
            else if (!password) document.getElementById('password').focus();
            else document.getElementById('repassword').focus();
        }

        e.preventDefault();
    }

    return (
        <section id="login" className="login">
            <div className="login-container">
                <div className="login-content">
                    <div className="login-header">
                        <h1>S-Shop</h1>
                    </div>
                    <div className="login-form">
                        <form method="get" action="/" onSubmit={handleRegister}>
                            <div className="form-group">
                                <label htmlFor="username">Tn ng nhp</label>
                                <input
                                    type="text"
                                    value={username}
                                    onChange={(e) => {
                                        setUsername(e.target.value);
                                    }}
                                    className="form-control"
                                    id="username"
                                    placeholder="Tn ng nhp"
                                />
                            </div>
                            <div className="form-group">
                                <label htmlFor="password">Mt khu</label>
                                <input
                                    type="password"
                                    value={password}
                                    onChange={(e) => {
                                        setPassword(e.target.value);
                                    }}
                                    className="form-control"
                                    id="password"
                                    placeholder="Mt khu"
                                />
                            </div>
                            <div className="form-group">
                                <label htmlFor="repassword">Nhp li mt khu</label>
                                <input
                                    type="password"
                                    className="form-control"
                                    value={repassword}
                                    onChange={(e) => {
                                        setRepassword(e.target.value);
                                    }}
                                    id="repassword"
                                    placeholder="Nhp li mt khu"
                                />
                            </div>

                            <div className="form-group form-group-button">
                                <button id="btn-register" type="submit" className="btn btn-login" value="Register">
                                    ng k
                                </button>
                            </div>
                            <div className="form-group form-group-link form-group-link-register">
                                <a href={configPaths.login}>Bn  c ti khon?Chuyn qua ng nhp</a>
                            </div>
                        </form>
                    </div>
                </div>
            </div>
        </section>
    );
}

export default Register;
