import { useEffect, useState } from 'react';
import { useParams } from 'react-router-dom';
import configPaths from '~/routes/configPaths';
import { getProductById } from '~/utils';
function ProductDetail() {
    const [product, setProduct] = useState('');
    const [productQuantity, setProductQuantity] = useState(1);
    let { id } = useParams();

    function handleAddToCart() {
        alert(' thm vo gi hng');
        let cart = [];
        cart = JSON.parse(localStorage.getItem('cart'));
        if (!cart) {
            cart = [{ product, productQuantity }];
            localStorage.setItem('cart', JSON.stringify(cart));
        } else {
            cart.push({ product, productQuantity });
            localStorage.setItem('cart', JSON.stringify(cart));
        }
        console.log(cart);
    }

    useEffect(() => {
        getProductById(id)
            .then(function (response) {
                // console.log(response);
                setProduct(response);
            })
            .catch(function (error) {
                // handle error
                console.log(error);
            });
        // eslint-disable-next-line
    }, []);
    return (
        <section className="product-detail">
            <div className="product-detail-container">
                <div className="product-top">
                    <div className="product-top-title">
                        <h3 className="heading-page">
                            <a href={configPaths.products}>Sn phm</a> / <span>Chi tit sn phm</span>
                        </h3>
                    </div>
                </div>
                <div id="product-detail">
                    <div className="product-detail-img">
                        <img src={'../' + product.image} alt={product.image} />
                    </div>
                    <div className="product-detail-info">
                        <h1>{product.name}</h1>
                        <p>{product.description}</p>
                        <p>Gi: {product.price}</p>
                        {/* {product.price.toFixed(3)} */}
                        <p>Gim gi: {product.discount}%</p>
                        <div>
                            <label htmlFor="quantity">S lng:</label>
                            <input
                                type="number"
                                value={productQuantity}
                                min="1"
                                max={product.number}
                                onChange={(e) => {
                                    setProductQuantity(e.target.value);
                                }}
                            />
                        </div>

                        <div>
                            <button className="btn btn-active">Mua ngay</button>
                            <button className="btn btn-cart" onClick={handleAddToCart}>
                                Thm vo gi hng
                            </button>
                        </div>
                    </div>
                </div>
            </div>
        </section>
    );
}

export default ProductDetail;
