import { useState } from 'react';
import { getUsersByParams } from '~/utils';
function Login() {
    const [username, setUsername] = useState('');
    const [password, setPassword] = useState('');
    function handleSubmit(e) {
        getUsersByParams({ username, password })
            .then(function (data) {
                let user = null;
                if (data.length > 0) {
                    user = data[0];
                    if (user.role === 'admin') console.log('admin');
                    else console.log('user');

                    localStorage.setItem('user', JSON.stringify(user));

                    alert('ng nhp thnh cng');
                } else {
                    alert('Sai thng tin ng nhp');
                    e.preventDefault();
                }
            })
            .catch(function (error) {
                // handle error
                console.log(error);
            });

        e.preventDefault();
    }

    return (
        <section id="login" className="login">
            <div className="login-container">
                <div className="login-content">
                    <div className="login-header">
                        <h1>S-Shop</h1>
                    </div>
                    <div className="login-form">
                        <form action="/" onSubmit={handleSubmit}>
                            <div className="form-group">
                                <label htmlFor="username">Tn ng nhp</label>
                                <input
                                    spellCheck="false"
                                    value={username.trim()}
                                    type="text"
                                    className="form-control"
                                    id="username"
                                    onChange={(e) => {
                                        setUsername(e.target.value);
                                    }}
                                    placeholder="Username"
                                />
                            </div>
                            <div className="form-group">
                                <label htmlFor="password">Mt khu</label>
                                <input
                                    value={password}
                                    type="password"
                                    className="form-control"
                                    id="password"
                                    placeholder="Password"
                                    onChange={(e) => {
                                        setPassword(e.target.value);
                                    }}
                                />
                            </div>
                            <div className="form-group form-group-button">
                                <button id="btn-login" type="submit" className="btn btn-login" value="Login">
                                    Login
                                </button>
                            </div>
                            <div className="form-group form-group-link">
                                <a href="/register">To ti khon mi mi</a>
                                <a href="/">Qun mt khu?</a>
                            </div>
                        </form>
                    </div>
                </div>
            </div>
        </section>
    );
}

export default Login;
